<template>

     <div>
    <my-nav-bar>
      <!--<span class="text-color-fe form-nav">家族详情</span>-->
      <search-input @keyword="handleKeyWord"></search-input>
    </my-nav-bar>

    <div class="app-family-main">

      <ul class="app-family-list-box js_fl_list">

        <!--<li v-for="item in list" :key="item.id">{{item.id}}</li>-->
        <family-list v-for="item in list" :key="item.id" v-bind:datalist="item"></family-list>

      </ul>


    </div>
  </div>

 
</template>

<script>
  import myNavBar from './myNavBar';
  import SearchInput from './SearchInput';
  import FamilyList from './FamilyList';

  export default {
    data() {
      return {
        list: [],
        server:"http://192.168.1.37:8080/qmzb'"
      }
    },
    beforeCreate:function(){
        //实例创建之前
          //加载数据
        let form_2 = new FormData();
        let server="http://139.199.23.160:8080/qmzb"
        form_2.append("keyword", "");
        form_2.append("page", 1);
        this.$http.post(server + "/check/getFamilyList", form_2).then(function (response) {
          //成功
            this.list=response.data.data.list;
          
        
        }.bind(this)).catch(function (erro) {
          //失败
        });

    },

    created:function(){
        //实例创建后执行
    },
    components: {
      myNavBar,
      SearchInput,
      FamilyList
    },
    methods: {
      dataLoad: function () {
        //加载数据
        this.$http.post("", {}).then(function (response) {
          //成功 
        }).catch(function (erro) {
          //失败
        });
       
      },
      handleKeyWord: function (msg) {
        let words = msg;


      }
    }

  }

</script>

